
import cookie from 'vux/src/tools/cookie/index.js'

import importJs from '../../../static/js/importJs'
import RoomVideo from "../RoomVideoNew"
import Product from "../Product"
import Comments from "../Comment"
export default {
name: 'room-common',
components:{
"room-video": RoomVideo,
"Product": Product,
"Comments": Comments,
},
data () {
return {
contabObj: {},
contentSwiper: {},
currentVideo : {},
playing: false,
playinit: false,
VideoCoverImg: "",
showCover: false,
galleryTop: {},
play_status : 1,
room_id: 0,
room_info: {},
showComment: true,
comment:"",
total_price: 0,
lens:{},
player: {},
deliver: 0, //起送金额
aliplayer_config: {
id: 'J_prismPlayer',
/*width: '100%',
height: '240px',*/
autoplay: false,
playsinline: true,
showBuffer: true,
isLive: true,
useH5Prism: !0,
useFlashPrism: !1,
x5_video_position: "normal",
x5_type: "h5",



source: '',
cover: '',
rePlay: false,
preload: false,
skinLayout: [
{name: 'bigPlayButton', align: 'cc', x: 30, y: 80},
{name: 'H5Loading', align: 'cc'},


{name: 'tooltip', align: 'blabs', x: 0, y: 56},
{name: 'thumbnail'},
{
name: 'controlBar', align: 'blabs', x: 0, y: 0,
children: [
{name: 'liveDisplay', align: 'tlabs', x: 15, y: 6},
{name: 'progress', align: 'blabs', x: 0, y: 44},
{name: 'playButton', align: 'tl', x: 15, y: 12},
{name: 'timeDisplay', align: 'tl', x: 10, y: 7},
{name: 'fullScreenButton', align: 'tr', x: 10, y: 12},




]
}
]
},
roomBasic: {
room_name: '',
start_num: '',
title: '',
sub_title: '',
intro: '',
click_num: '',
